*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

:root {

  /* Colors */
  --colorMain1: #ffffff;
  --colorMain2: #ffffff;
  --colorBg1: #000000; 

  /* Fonts */
  --fontRegText1: Arial, Helvetica, sans-serif;
  --fontHeadingText1: Arial, Helvetica, sans-serif;
}

h1 {
  font-family: var(--fontHeadingText1);
  color: var(--colorMain1);
  font-size: 3rem;
}

/* Header */

header div {
  display: flex;
  justify-content: center;
  padding: 1.5rem 0;
  width: 100%;
  background-color: var(--colorBg1);
}

header .header__logo {
  max-width: 3rem;
}

/* Main */

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: calc(100vh - calc(3rem + 55.3594px));
  background-color: #000000;
}

main section:nth-child(1) {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 3rem 0;
  /* background-color: antiquewhite; */
}

main section:nth-child(2) {
  /* background-color: #f230ce; */
  display: flex;
  justify-content: center;
  cursor: grab;
}

.bottomText {
  color: var(--colorMain1);
  position: absolute;
  width: 100vw;
  bottom: 0;
}

.bottomText h2 {
  width: max-content;
  margin: 0 auto;
  padding: 3rem;
  font-weight: 400;
}




